const _isEqual = require('lodash/isEqual')

/**
 * 
 * @module utils/util
 */

/**
 * 
 * @param {*} object
 * @param {*} other
 * @return {boolean}
 */
export function isEqual(object, other) {
  return _isEqual(object, other)
}

/**
 * 
 * @param {function} fn 
 * @param {number} [delay=20] 
 * @param {boolean} [isImmediate=false] 
 * @param {object} [context=this] 
 * @returns {Function} 
 */
export function debounce(fn, delay = 20, isImmediate = false, context = this) {
  // 
  let timer;
  
  return function () {
    const _args = [].slice.call(arguments)
    
    clearTimeout(timer);
    
    const _fn = function () {
      timer = null;
      if (!isImmediate) fn.apply(context, _args);
    };
    
    // 
    const callNow = !timer && isImmediate;
    
    timer = setTimeout(_fn, delay);
    
    if (callNow) fn.apply(context, _args);
  }
}

const raFrame = window.requestAnimationFrame || window.webkitRequestAnimationFrame || function (callback) {
  return window.setTimeout(callback, 1000 / 60);
};

/**
 * 
 * @function
 * @param {function} callback 
 * @return {number} id
 */
export const requestAnimationFrame = raFrame

/**
 * 
 * @function
 * @param {number} id
 */
export const cancelAnimationFrame = window.cancelAnimationFrame || window.webkitRequestAnimationFrame || function (id) {
  window.clearTimeout(id);
};

/**
 * 
 * @param {function} fn 
 * @param {object} [context=this] 
 * @param {boolean} [isImmediate=false] 
 * @returns {Function} 
 */
export function throttle(fn, context = this, isImmediate = false) {
  let isLocked;
  return function () {
    const _args = arguments
    
    if (isLocked) return
    
    isLocked = true
    raFrame(function () {
      isLocked = false;
      fn.apply(context, _args)
    })
    
    isImmediate && fn.apply(context, _args)
  }
}

/**
 * 
 * @param {Array|Object} data  {id:1, children:[{id:2}]}
 * @param {Boolean} isFindOne 
 * @param {Function} fn item indexdata true
 * @param {string} [field=children] 
 * @returns {Array|Object} isFindOnetrueObject falseArray
 */
export function traverse(data = [], isFindOne, fn, field = 'children') {
  let result = []
  data = Array.isArray(data) ? data : [data]
  for (let i = 0, len = data.length; i < len; i++) {
    const item = data[i],
      checked = fn(item, i, data),
      children = item[field]
    if (checked) {
      result.push(item)
      if (isFindOne) break
    }
    if (children) {
      const child = traverse(children, isFindOne, fn, field)
      if (child) result = result.concat(child)
    }
  }
  return isFindOne ? result[0] || null : result
}

/**
 * 
 * @param {Array|Object} data   {id:1, children:[{id:2}]}
 * @param {Function} fn item indexdata true
 * @param {string} [field=children] 
 * @return {Array} 
 */
export function findPath(data, fn, field = 'children') {
  let path = []
  
  function find(array, parent) {
    parent && path.push(parent)
    for (let i = 0, len = array.length; i < len; i++) {
      const item = array[i],
        checked = fn(item, i, array),
        children = item[field]
      // 
      if (checked) {
        path.push(item)
        return true
      }
      if (children && children.length > 0) {
        // 
        if (find(children, item)) {
          return true
        } else {
          path.pop()
        }
      }
    }
  }
  
  find([].concat(data))
  return path
  
}

/**
 * url
 * @param {string} str 
 * @returns {boolean}
 */
export function isUrl(str) {
  return (/^(http|https):\/\/*/.test(str))
}

/**
 * 
 * @param {string} str 
 * @returns {boolean}
 */
export function isEmail(str) {
  return (/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(str))
}

/**
 * 
 * @param {string} str 
 * @returns {boolean}
 */
export function isIdCard(str) {
  return (/^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(str))
}

/**
 * 
 * @param {string} str 
 * @returns {boolean}
 */
export function isPhoneNumber(str) {
  return (/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(str))
}

/**
 * GUID
 * @return {string}
 */
export function guid() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  }).toUpperCase();
}
