<template>
  <el-select
    v-model="s_value"
    v-on="$listeners"
    v-bind="$attrs"
    :dataType='dataType'
  >
    <slot>
      <el-option
        v-for="item in options"
        :key='item.value'
        :label="item.label"
        :value="item.value"
        :disabled="item.disabled"
      ></el-option>
    </slot>
    <slot
      name='prefix'
      slot="prefix"
    ></slot>
    <slot
      name='empty'
      slot="empty"
    ></slot>
  </el-select>
</template>
 
<script>
export default {
  name: "com_select",
  /***
   * 
   * @property {Object[]} value 
   * @property {list[]} list 
   * @property {Boolean} needAll false
   * @property {Object} fillType {'label':'label','value':'value'}
   * @property {String} dataType 
   *
   */
  props: {
    value: {},
    list: {
      type: Array,
      default: () => []
    },
    needAll: {
      type: Boolean,
      default: () => false
    },
    fileType: {
      type: Object,
      default: function() {
        return {
          label: "label",
          value: "value"
        };
      }
    },
    dataType: ""
  },
  data() {
    return {
      s_value: this.value,
      options: []
    };
  },
  watch: {
    value(val) {
      this.s_value = val;
    },
    s_value(val) {
      this.$emit("input", val);
    },
    list(val) {
      this.options = this.fileTypeFn(val);
      if (this.needAll) {
        this.options = this.needAllFn(this.options);
      }
    }
  },
  mounted() {
    this.options = this.fileTypeFn(this.list);
    if (this.needAll) {
      this.options = this.needAllFn(this.options);
    }
  },
  methods: {
    fileTypeFn(data) {
      let _data = [];
      data.forEach((val, i) => {
        _data[i] = {
          label: val[this.fileType.label],
          value: val[this.fileType.value],
          disabled: val[this.fileType.disabled] || false
        };
      });
      return _data;
    },
    needAllFn(data) {
      const num = JSON.stringify(data).includes(
        JSON.stringify({ label: "", value: "" })
      );
      if (!num) {
        data.unshift({ label: "", value: "" });
      }
      return data;
    }
  }
};
</script>