/**
 *  Axios , ajax
 *   [https://github.com/axios/axios]{@link https://github.com/axios/axios}
 *  @author 
 *  @module utils/axios
 */

import axios from 'axios'
// vuex
import store from '@/store'
// 
import router from '../router'
// API_HOST
import { API_HOST, AJAX_SUCCESS } from '@/config'

import { Message } from 'element-ui'
/**
 * 
 * @type {CancelTokenSource}
 * @example
 *
 * import {source} from '@/utils/axios'
 * source.cancel('....')
 */
export const source = axios.CancelToken.source()

/**
 * Axios
 * @const
 * @type {object}
 * @property {object} headers null
 * @property {number} timeout 0 
 * @property {boolean} withCredentials  true
 * @property {number} maxContentLength -1 
 * @property {strin} cancelToken 
 */
const config = {
  headers: {
    'Content-Type': 'application/json;charset=UTF-8'
  },
  timeout: 0,
  withCredentials: true,
  responseType: 'json',
  maxContentLength: -1,
  cancelToken: source.token,
  baseURL: API_HOST
}

const service = axios.create(config)

/**
 * 
 */
service.interceptors.request.use(config => {
  /**
   *  
   */
  config.cancelToken = new axios.CancelToken(function (cancel) {
    store.commit('pushToken', {cancelToken: cancel})
  })
  // tokenhttpheadertoken
  // tokentoken
  const token = store.state.token;
  token && (config.headers.Authorization = token);
  return config;
},
  error => {
    Message.error({
      message: ''
    })
    return Promise.error(error);
  }
)

// 
service.interceptors.response.use(
  res => {
    if (res.status === AJAX_SUCCESS) {
      return Promise.resolve(res.data);
    } else {
      return Promise.reject(res.data);
    }
  },
  // 200    
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401:                 
        //                 
        //                 
        case 401:
          router.replace({
            path: '/login',
            query: { redirect: router.currentRoute.fullPath }
          });
          break;
        // 403 token                
        //                 
        // tokenvuextoken                
        //                 
        case 403:
          Message.error({
            message: ''
          });
          // // token                    
          // localStorage.removeItem('token');
          // store.commit('loginSuccess', null);
          // fullPath
          setTimeout(() => {
            router.replace({
              path: '/login',
              query: {
                redirect: router.currentRoute.fullPath
              }
            });
          }, 1000);
          break;
        // 404                
        case 404:
          Message.error({
            message: ''
          });
          break;
        //                 
        default:
          Message.error({
            message: error.response.data.message
          });
      }
      return Promise.reject(error.response);
    }
  }
);


/**
 * Axios 
 * @example
 *
 *  // 
 *  import axios from '@/utils/axios'
 *  axios({
 *    method: 'post',
 *    url: '/user/123',
 *    data: {
 *      firstName: 'Fred',
 *      lastName: 'Flintstone'
 *    }
 *  })
 *
 *  @example
 *
 *  // 
 *  axios.request(config)
 *  axios.get(url[, config])
 *  axios.delete(url[, config])
 *  axios.head(url[, config])
 *  axios.options(url[, config])
 *  axios.post(url[, data[, config]])
 *  axios.put(url[, data[, config]])
 *  axios.patch(url[, data[, config]])
 */

 /**
 * 
 * @param {object} options 
 * 
 * url
 * methodget
 * data
 */
export default function (options) {
  // 
  let config = Object.assign({ method: 'get' }, options || {})

  // url
  if (!config.url) {
    throw new Error('ajax url is required!')
  }

  let { url, method, data } = config

  delete config.url
  delete config.method
  delete config.data

  const http = ['get', 'head', 'delete'].includes(method) ? service[method](url, {
    ...config,
    params: data
  }) : service[method](url, data, config)

  return http
} 
