/**
 * sessionStoragelocalStorage
 * @module utils/storage
 * @author 
 */

/**
 * sessionStorage
 * @const
 * @type {Storage}
 */
export const SESSION = sessionStorage

/**
 * localStorage
 * @type {Storage}
 */
export const LOCAL = localStorage

/**
 * 
 * @param {string} key key
 * @param {String|Object|Array} value JSON.stringify
 * @param {Storage} [storage=sessionStorage]  SESSION  LOCAL
 */
export function save(key, value, storage = SESSION) {
  const str = typeof value === 'object' ? JSON.stringify(value) : value
  storage.setItem(key, str)
}

/**
 * 
 * @param {string} key key
 * @param {Storage} [storage=sessionStorage]  SESSION  LOCAL
 * @returns {String|Object|Array}
 */
export function get(key, storage = SESSION) {
  let info = storage.getItem(key)
  if (info) {
    const regex = /^\{[\W\w]*\}$/
    return regex.test(info) ? JSON.parse(info) : info
  }
  return null
}

/**
 * 
 * @param {string} key key
 * @param {Storage} [storage=sessionStorage]  SESSION  LOCAL
 */
export function remove(key, storage = SESSION) {
  storage.removeItem(key)
}

/**
 * 
 * @param {Storage} [storage=sessionStorage]  SESSION  LOCAL
 */
export function clear(storage = SESSION) {
  storage.clear()
}
