const MyPlugin = {};

MyPlugin.install = function (Vue, options) {
    // 1. 
    Vue.myGlobalMethod = function () {
        // ...
    }

    // 2. 
    Vue.directive('my-directive', {
        bind(el, binding, vnode, oldVnode) {
            // ...
        }
    })

    // 3. 
    Vue.mixin({
        created: function () {
            // ...
        }
    })

    // 
    Vue.prototype.$dialog = function (title, content, opts) {
        const vm = new Vue({
            router: this.$router,
            store: this.$store,
            data: {
                title: title,
                visible: true
            },
            // 
            // extends: base,
            methods: {
                dismiss() {
                    this.visible = false
                    setTimeout(() => {
                        document.body.removeChild(this.$el)
                        opts && opts.on && opts.on.closed && opts.on.closed()
                    }, 100)
                },
                close(res) {
                    this.visible = false
                    opts && opts.on && opts.on.closed && opts.on.closed(res ? res : '')
                }
            },
            render(h) {
                const dialogOption = {
                    props: {
                        ...(opts && opts.props || {})
                    },
                    on: {
                        ...(opts && opts.on || {}),
                        close: (b) => this.close(b)
                    }
                }
                return h('el-dialog', {
                    props: {
                        title: this.title,
                        center: true,
                        visible: this.visible,
                        'show-close': true,
                        width: opts.width
                    },
                    on: {
                        ['updata:visible']: () => this.close(),
                        close: () => this.close(),
                        closed: () => this.dismiss()
                    }
                }, [h('ElContent', {
                    refInFor: true,
                    ...dialogOption
                })]);
            },
            components: {
                //
                // ElBase: base,
                // 
                ElContent: content
            }
        });
        // 
        document.body.appendChild(vm.$mount().$el);
    }
}
export default MyPlugin